import { Nonce } from './nonce';
import { UrlEventType, UrlParameter } from '../requester';
import {
  BrowserDataType,
  BrowserType,
  IKameleoonData,
  KameleoonData,
} from './types';

/**
 * @class
 * Browser - a class for creating an instance for browser of a certain type
 * */
export class Browser extends Nonce implements IKameleoonData {
  private browser: BrowserType;

  /**
   * @param {BrowserType} browser - one of the browser types available for data, if browser can not be found in a list, it can be marked as `BrowserType.Other`
   * */
  constructor(browser: BrowserType) {
    super();

    this.browser = browser;
  }

  /**
   * @property url - url for the current data type used for events tracking
   * */
  get url(): string {
    return (
      UrlEventType.StaticData +
      UrlParameter.BrowserIndex +
      this.browser +
      UrlParameter.Nonce +
      this.nonce
    );
  }

  /**
   * @property data - data of the current class instance
   * */
  get data(): BrowserDataType {
    return {
      browser: this.browser,
      type: KameleoonData.Browser,
      nonce: this.nonce,
    };
  }
}
